	module.exports = "<pre><code class=\"hljs html\"><span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>v-dialog:toggle</span>=<span class=hljs-string>\"form-modal\"</span>&gt;</span>Open A11Y Dialog<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>wv-dialog</span>\n  <span class=hljs-attr>class</span>=<span class=hljs-string>\"form-dialog\"</span>\n  @<span class=hljs-attr>closed</span>=<span class=hljs-string>\"active = false\"</span>\n  @<span class=hljs-attr>opened</span>=<span class=hljs-string>\"active = true\"</span>\n  <span class=hljs-attr>ref</span>=<span class=hljs-string>\"form-modal\"</span>\n  <span class=hljs-attr>aria-labelledby</span>=<span class=hljs-string>\"modal-title\"</span>\n&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>wv-focus-trap</span>\n    <span class=hljs-attr>class</span>=<span class=hljs-string>\"form-dialog__content\"</span>\n    <span class=hljs-attr>:active</span>=<span class=hljs-string>\"active\"</span>\n    <span class=hljs-attr>:deactivate-on-outside-click</span>=<span class=hljs-string>\"true\"</span>\n  &gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>h1</span> <span class=hljs-attr>id</span>=<span class=hljs-string>\"modal-title\"</span>&gt;</span>Modal title<span class=hljs-tag>&lt;/<span class=hljs-name>h1</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>form</span> @<span class=hljs-attr>submit.prevent</span>=<span class=hljs-string>\"onSubmit\"</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>label</span>&gt;</span>Name: <span class=hljs-tag>&lt;<span class=hljs-name>input</span> <span class=hljs-attr>required</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"text\"</span> <span class=hljs-attr>placeholder</span>=<span class=hljs-string>\"Give me your name\"</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>label</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>label</span>&gt;</span>Email:<span class=hljs-tag>&lt;<span class=hljs-name>input</span> <span class=hljs-attr>required</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"email\"</span> <span class=hljs-attr>placeholder</span>=<span class=hljs-string>\"Give me your email\"</span>&gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>label</span>&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"submit\"</span> <span class=hljs-attr>name</span>=<span class=hljs-string>\"button\"</span>&gt;</span>Submit<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n    <span class=hljs-tag>&lt;/<span class=hljs-name>form</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>wv-focus-trap</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>wv-dialog</span>&gt;</span>\n</code></pre> <pre><code class=\"hljs js\"><span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> {\n  data () {\n    <span class=hljs-keyword>return</span> {\n      active: <span class=hljs-literal>false</span>\n    }\n  },\n  methods: {\n    onSubmit () {\n      <span class=hljs-keyword>this</span>.$wv.dialog.toggle(<span class=hljs-string>'form'</span>)\n    }\n  }\n}\n</code></pre> ";

